import React from 'react';
import { Helmet } from 'react-helmet';

interface SEOProps {
  title?: string;
  description?: string;
  keywords?: string;
  canonicalUrl?: string;
  ogImage?: string;
  cityName?: string;
  isHomePage?: boolean;
}

const SEO: React.FC<SEOProps> = ({
  title = '    | VIP  ',
  description = '   .  VIP     .     .',
  keywords = ' , vip ,  , escort services, ',
  canonicalUrl = 'https://eskortvsegorodarfreal.site',
  ogImage = 'https://eskortvsegorodarfreal.site/logo192.png',
  cityName,
  isHomePage = false
}) => {
  //   ,  -
  const finalTitle = cityName 
    ? ` ${cityName} | -  VIP  ` 
    : title;
    
  const finalDescription = cityName 
    ? `  ${cityName}.  VIP    .  ${cityName}   .     .` 
    : description;
    
  const finalKeywords = cityName 
    ? ` ${cityName},  ${cityName}, vip  ${cityName},  ${cityName},   ${cityName},   ${cityName},   ${cityName}` 
    : keywords;
    
  const citySlug = cityName ? cityName.toLowerCase().replace(/\s+/g, '-') : '';
  const finalCanonicalUrl = cityName 
    ? `https://eskortvsegorodarfreal.site/${citySlug}` 
    : canonicalUrl;
    
  //  Schema.org    JSON-LD
  const generateSchemaOrgJSONLD = () => {
    //     
    const baseSchema = {
      '@context': 'https://schema.org',
      '@type': 'WebSite',
      'url': 'https://eskortvsegorodarfreal.site',
      'name': '   ',
      'description': '   VIP    '
    };
    
    //     
    if (cityName) {
      const localBusinessSchema = {
        '@context': 'https://schema.org',
        '@type': 'LocalBusiness',
        'name': `   ${cityName}`,
        'description': finalDescription,
        'address': {
          '@type': 'PostalAddress',
          'addressLocality': cityName,
          'addressCountry': ''
        },
        'url': finalCanonicalUrl
      };
      
      return [
        baseSchema,
        localBusinessSchema
      ];
    }
    
    //  BreadcrumbList  SEO     
    if (!isHomePage) {
      const breadcrumbSchema = {
        '@context': 'https://schema.org',
        '@type': 'BreadcrumbList',
        'itemListElement': [
          {
            '@type': 'ListItem',
            'position': 1,
            'name': '',
            'item': 'https://eskortvsegorodarfreal.site'
          },
          {
            '@type': 'ListItem',
            'position': 2,
            'name': cityName || '',
            'item': finalCanonicalUrl
          }
        ]
      };
      
      return [baseSchema, breadcrumbSchema];
    }
    
    return [baseSchema];
  };

  return (
    <Helmet>
      <title>{finalTitle}</title>
      <meta name="description" content={finalDescription} />
      <meta name="keywords" content={finalKeywords} />
      <link rel="canonical" href={finalCanonicalUrl} />
      
      {/* Open Graph       */}
      <meta property="og:title" content={finalTitle} />
      <meta property="og:description" content={finalDescription} />
      <meta property="og:type" content="website" />
      <meta property="og:url" content={finalCanonicalUrl} />
      <meta property="og:image" content={ogImage} />
      <meta property="og:locale" content="ru_RU" />
      
      {/* Twitter Card  */}
      <meta name="twitter:card" content="summary_large_image" />
      <meta name="twitter:title" content={finalTitle} />
      <meta name="twitter:description" content={finalDescription} />
      <meta name="twitter:image" content={ogImage} />

      {/*  - */}
      <meta name="robots" content="index, follow, max-image-preview:large" />
      <meta name="language" content="Russian" />
      <meta name="revisit-after" content="1 days" />
      <meta name="author" content="Escort Services" />
      <meta name="geo.region" content="RU" />
      {cityName && <meta name="geo.placename" content={cityName} />}
      
      {/* Schema.org JSON-LD  */}
      <script type="application/ld+json">
        {JSON.stringify(generateSchemaOrgJSONLD())}
      </script>
    </Helmet>
  );
};

export default SEO; 